import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import "./NavBarMobile.css";
import logo from "../../../../Assets/Images/logo.svg";
import Auxillary from "../../../../HOC/Auxillary/Auxillary";

class NavBarMobile extends Component {
  state = { pricing: false };

  liClickHandler = () => {
    this.setState({ pricing: true });
  };

  render() {
    return (
      <Auxillary>
        <div className="Nav-top" onClick={this.props.close}>
          <NavLink to="/" className="Nav-top-logo">
            <img src={logo} alt="Logo" />
          </NavLink>
        </div>
        <ul className="Nav-mainMenu-mobile ul-reset-mobile">
          <li className="Nav-Submenu-mobile">
            <span className="suite-we-offer">Services We Offer</span>
            <ul className="mobile-view">
              <li onClick={this.props.close}>
                <NavLink
                  to="/invoicing"
                  style={{ textDecoration: "none" }}
                  className="Nav-linkli-mobile"
                >
                  Invoicing
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/invoicing"
                  style={{ textDecoration: "none" }}
                  className="Nav-linkli-mobile"
                >
                  Invoicing
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/invoicing"
                  style={{ textDecoration: "none" }}
                  className="Nav-linkli-mobile"
                >
                  Invoicing
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/invoicing"
                  style={{ textDecoration: "none" }}
                  className="Nav-linkli-mobile"
                >
                  Invoicing
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/invoicing"
                  style={{ textDecoration: "none" }}
                  className="Nav-linkli-mobile"
                >
                  Invoicing
                </NavLink>
              </li>
            </ul>
          </li>
          <li className={"link-mobile"} onClick={this.props.close}>
            <NavLink
              className="Nav-link-mobile"
              activeClassName="is-active"
              style={{ textDecoration: "none" }}
              to="/pricing"
            >
              Pricing
            </NavLink>
          </li>
          <li className="link-mobile" onClick={this.props.close}>
            <NavLink
              className="Nav-link-mobile"
              style={{ textDecoration: "none" }}
              to="/reviews"
            >
              Reviews
            </NavLink>
          </li>
          <li className="link-mobile">
            <NavLink
              className="Nav-link-mobile"
              style={{ textDecoration: "none" }}
              to="/About"
            >
              About
            </NavLink>
          </li>
          <li className="link-mobile">
            <NavLink
              className="Nav-link-mobile"
              style={{ textDecoration: "none" }}
              to="/gopro"
            >
              Go PRO
            </NavLink>
          </li>
        </ul>
        <ul className="Nav-webApp u-reset-ul">
          <li className="Nav-signUp-mobile">
            <NavLink
              className="Nav-webApp-link-mobile Nav-webApp-link Nav-webApp-link--signUp Nav-signUpButton signupNavLink Nav-link-mobile"
              style={{ textDecoration: "none" }}
              to="/signup"
            >
              <span>Sign up for free</span>
            </NavLink>
          </li>
          <li className="Nav-logIn-mobile">
            <NavLink
              className="Nav-webApp-link Nav-webApp-link--signIn Nav-signInButton Nav-link-mobile"
              style={{ textDecoration: "none" }}
              to="/login"
            >
              Log In
            </NavLink>
          </li>
        </ul>
      </Auxillary>
    );
  }
}

export default NavBarMobile;
